import '../../assets/style/sec-container.scss'
import React from 'react'

export interface ISecItemProps {
    imgPath: any
    title: string
    content: string
}

export class SectionItem extends React.Component<ISecItemProps>{
    render() {
        return (
            <div className='sub-feature-inner-container'>
                <div className='split'></div>
                <div className='img-div' style={{ backgroundImage: 'url(' + this.props.imgPath + ')' }}></div>
                {/* <img src={this.props.imgPath} alt='' /> */}
                <h5>{this.props.title}</h5>
                <p>{this.props.content}</p>
            </div>
        )
    }
}

export interface ISecProps {
    type: string
    title?: string
    content?: string
    img?: any[]
    items?: ISecItemProps[]
}

export default class SecContainer extends React.Component<ISecProps> {
    static ROW_SEC: string = 'row_sec';
    static COL_SEC: string = 'col_sec';

    render() {
        switch (this.props.type) {
            case SecContainer.ROW_SEC:
                const items: JSX.Element[] | undefined = this.props.items?.map((itemProp: ISecItemProps) => {
                    return <SectionItem imgPath={itemProp.imgPath} title={itemProp.title} content={itemProp.content} />
                })
                return (
                    <div className='feature-container'>
                        <div className='feature-inner-container-row'>
                            {items}
                        </div>
                    </div>
                );
            case SecContainer.COL_SEC:
                const imgs: JSX.Element[] | undefined = this.props.img?.map((imgPath: any) => <img src={imgPath} alt='' />)
                return (
                    <div className='feature-container'>
                        <div className='split'></div><br></br>
                        <h4>{this.props.title}</h4>
                        <div className='feature-inner-container-col'>
                            <div className='feature-text'>
                                <p>{this.props.content}</p>
                            </div>
                            <div className='feature-illus'>
                                {imgs}
                            </div>
                        </div>
                    </div>
                );
        }

    }
}

