import '../../assets/style/gallery.scss'

import React from 'react'
import Nav from '../layout/nav'
import AniTxtCard from '../layout/aniTxtCard'
import Footer from '../layout/footer'

import mushroomLottie from '../../assets/img/gallery/mushroom.json';
import { Link } from 'react-router-dom'

export default class Gallery extends React.Component {
    render() {
        const mushroomLink: any = {
            pathname: '/gallery-detail',
            name: 'mushroom'
        }
        return (
            <div>
                <Nav activeTab='gallery' />
                <div className='container'>
                    <div className='gallery-list'>
                        <Link to='/gallery-detail/mushroom'><AniTxtCard cName='mushroom' lottie={mushroomLottie} /></Link>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}